.modal {
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
	overflow: hidden;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.8);
	width: 100%;
	height: 100%;
	padding: 1.5em 1em;
	z-index: 999999;
}

.modal.modalFade {
	animation: fade-in 1s 1 linear;
	animation-fill-mode: forwards;
	opacity: 0;
}

.modal > .modalOverlay {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}

.modal > .modalClose {
	display: inline-block;
	position: absolute;
	right: 15px;
	top: 10px;
	color: #5e5e5e;
	cursor: pointer;
	font-size: 1.25em;
	padding: 7px;
	background: rgba(255, 255, 255, 0.749);
	border: 1px solid #c3c0c0;
	border-radius: 50%;
	width: 42px;
	height: 42px;
	z-index: 999999;
	text-align: center;
}

.modal > .modalClose:hover {
	background: rgba(255, 255, 255, 0.989);
}

.modal > .modalBody {
	position: relative;
	z-index: 2;
	margin: 0 auto;
	border-radius: 3px;
	overflow-x: hidden;
	overflow-y: auto;
	max-height: 100%;
	padding: 15px 20px;
	color: #c3c0c0;
	width: 100%;

	@media screen and (max-width: 800px) {
		width: 100%;
		padding: 0;
	}
}

.modal.loading {
	background-color: transparent;

	& > .modalBody {
		position: absolute;
		top: 0;

		@media screen and (max-width: 800px) {
			width: auto;
			padding: 20px;
		}
	}
}

@keyframes fade-in {
	0% {
		animation-timing-function: cubic-bezier(0.2242, 0.7499, 0.3142, 0.8148);
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}
